import React, { useContext, useState } from 'react'

import { CFormInput } from '@coreui/react';
import LockIcon from '@mui/icons-material/Lock';
import '@coreui/coreui/dist/css/coreui.min.css'
import { Button } from '@mui/material';
import LocalPostOfficeIcon from '@mui/icons-material/LocalPostOffice';
import axios from 'axios'
import toast from 'react-hot-toast';
import { useNavigate } from 'react-router-dom'
import { AppContext } from '../components/contextsAPI/authContext';


const Login = () => {
    const [data, setData] = useState(
        {

            email: "",
            password: "",

        }
    );
    const navigate = useNavigate();

    // auth//

    // useContext(AppContext)
    const [auth, setAuth] = useContext(AppContext);


    const inputEvent = (e) => {

        const name = e.target.name;
        const value = e.target.value;
        setData((prev) => {
            return { ...prev, [name]: value }
        })



    }
    const handleSubmit = async (e) => {
        e.preventDefault();
        // console.log(data.email)
        try {
            const { email, password } = data;
            // console.log(password, email, phone, name)


            console.log(email, password)
            const res = await axios.post('/api/v1/auth/login', { email, password })
            console.log(res);

            if (res.data.success) {
                toast.success("Login Successfully")

                console.log(res)
                setAuth({
                    ...auth,
                    user: res.data.user,
                    status: res.data.status,
                })

                localStorage.setItem('auth', JSON.stringify(res.data))
                navigate('/')
            }

            else {
                toast.error("Invalid Details")
                console.log("Error")
            }


        } catch (e) {
            console.log(e)
        }

    }
    const registerEvent = () => {
        navigate('/register')
    }


    return (
        <div className='form_container'>
            {/* <ToastContainer /> */}
            <div className="formBox">

                <div className="form">
                    <form action="" onSubmit={handleSubmit}>
                        <div>
                            <h4>Login</h4>
                        </div>
                        <div className="texts">

                            {/* <div>
                                <LocalPostOfficeIcon />
                                <CFormInput className='inputs' value={data.name} name='name' type="text" size="sm" placeholder="Enter your full name" aria-label="sm input example" onChange={inputEvent} />
                                
                            </div> */}
                            <div>
                                <LocalPostOfficeIcon />
                                <CFormInput required className='inputs' value={data.email} name='email' type="email" size="sm" placeholder="Enter Email" aria-label="sm input example" onChange={inputEvent} />

                            </div>
                            <div>
                                <LockIcon />
                                <CFormInput required value={data.password} className='inputs' name='password' type="text" size="sm" placeholder="Enter password" aria-label="sm input example" onChange={inputEvent} />

                            </div>
                            {/* <div>
                                <LockIcon />
                                <CFormInput value={data.phone} className='inputs' name='phone' type="number" size="sm" placeholder="Enter Phone Number" aria-label="sm input example" onChange={inputEvent} />
                                
                            </div> */}
                            <div>
                                <Button type='submit'>Login</Button>

                            </div>
                            <p>Don't Have Account? <span onClick={registerEvent}>Register</span></p>
                        </div>

                    </form>
                </div>
                <div className="image">
                    <img src="./Images/login.svg" alt="Login Form " />
                </div>
            </div>
        </div>
    )
}

export default Login


