import React, { useState } from 'react'
import TextField from '@mui/material/TextField';
import { CFormInput } from '@coreui/react';
import LockIcon from '@mui/icons-material/Lock';
import '@coreui/coreui/dist/css/coreui.min.css'
import { Button } from '@mui/material';
import LocalPostOfficeIcon from '@mui/icons-material/LocalPostOffice';
import axios from 'axios'
import toast, { Toaster } from 'react-hot-toast';
import { useNavigate } from 'react-router-dom'
import PersonIcon from '@mui/icons-material/Person';
import PhoneIcon from '@mui/icons-material/Phone';

const Register = () => {
    const [data, setData] = useState(
        {
            name: "",
            email: "",
            password: "",
            phone: ""
        }
    );

    const navigate = useNavigate();

    const inputEvent = (e) => {

        const name = e.target.name;
        const value = e.target.value;
        setData((prev) => {
            return { ...prev, [name]: value }
        })



    }
    const handleSubmit = async (e) => {
        e.preventDefault();
        // console.log(data.email)
        try {
            const { name, email, password, phone } = data;
            // console.log(password, email, phone, name)



            const res = await axios.post('/api/v1/auth/register', { name, email, password, phone })

            if (res.data.success) {
                toast.success("Register Successfully")
                navigate('/login')
            }
            else {
                toast.error(`user : ${email} already registered`)

            }
            // console.log(res.data.success)
        } catch (e) {
            console.log(e)
            toast.error("Something went Wrong")
        }

    }
    const loginEvent = () => {
        navigate('/login')
    }
    return (
        <div className='form_container'>
            {/* <ToastContainer /> */}
            <div className="formBox">

                <div className="form">
                    <form action="" onSubmit={handleSubmit}>
                        <div>
                            <h4>Create An Account</h4>
                        </div>
                        <div className="texts">

                            <div>
                                <PersonIcon />
                                <CFormInput required className='inputs' value={data.name} name='name' type="text" size="sm" placeholder="Enter your full name" aria-label="sm input example" onChange={inputEvent} />
                                {/* <input type="text" placeholder='Enter Email' /> */}
                            </div>
                            <div>
                                <LocalPostOfficeIcon />
                                <CFormInput required className='inputs' value={data.email} name='email' type="text" size="sm" placeholder="Enter Email" aria-label="sm input example" onChange={inputEvent} />
                                {/* <input type="text" placeholder='Enter Email' /> */}
                            </div>
                            <div>
                                <LockIcon />
                                <CFormInput required value={data.password} className='inputs' name='password' type="text" size="sm" placeholder="Enter Password" aria-label="sm input example" onChange={inputEvent} />
                                {/* <input type="text" placeholder='Enter Email' /> */}
                            </div>
                            <div>
                                <PhoneIcon />
                                <CFormInput required value={data.phone} className='inputs' name='phone' type="number" size="sm" placeholder="Enter Phone Number" aria-label="sm input example" onChange={inputEvent} />
                                {/* <input type="text" placeholder='Enter Email' /> */}
                            </div>
                            <div>
                                <Button type='submit'>Register</Button>

                            </div>
                            <p>Have an Account? <span onClick={loginEvent}>Login</span></p>
                        </div>

                    </form>
                </div>
                <div className="image">
                    <img src="./Images/login.svg" alt="Login Form " />
                </div>
            </div>
        </div>
    )
}

export default Register


