import React, { useContext } from 'react'
import Button from '@mui/material/Button';
import { NavLink } from 'react-router-dom'
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LoginIcon from '@mui/icons-material/Login';
import HomeIcon from '@mui/icons-material/Home';
import './style.css'
import Layout from './Layout';
import toast from 'react-hot-toast';

import { AppContext } from './contextsAPI/authContext';
import LordIcon from './LordIcon';
// import { Helmet } from "react-helmet";

const Navbar = () => {

    const [auth, setAuth] = useContext(AppContext);

    console.log(`name is ${auth.status}`)
    const details = {
        // title: "Navbar",
        author: "Vipul"
    }

    const logoutHandel = () => {
        setAuth({
            ...auth,
            user: null,
            status: false,
        })
        toast.success('Successfully Log out!');


        localStorage.removeItem('auth')
    }
    // < h1 > { JSON.stringify(auth.status) }</>
    return (
        <>
            <Layout props={details} />
            {/* <pre>{JSON.stringify(auth, null, 4)}</pre> */}

            <div>
                {

                    (!auth.status) ? (<>
                        <nav>
                            <div>
                                <h1>E-Commerce</h1>

                            </div>
                            <ul>
                                <div>
                                    <NavLink className="navlinks" to="/"><LordIcon img="https://cdn.lordicon.com/kxoxiwrf.json" /> Home</NavLink>
                                    <NavLink className="navlinks" to="/login"> <LoginIcon /> Login</NavLink>
                                </div>
                                <div>
                                    <NavLink className="navlinks" to="/service"><SearchIcon /></NavLink>
                                    <NavLink className="navlinks" to="/cart"><ShoppingCartIcon /> Cart</NavLink>
                                </div>


                            </ul>
                        </nav>
                    </>) :
                        (<>
                            <nav>
                                <div>
                                    <h1>E-Commerce</h1>

                                </div>
                                <ul>
                                    <div>
                                        <NavLink className="navlinks" to="/"><HomeIcon /> Home</NavLink>
                                        <NavLink className="navlinks" to="/login" onClick={logoutHandel}> <LoginIcon /> Logout</NavLink>
                                    </div>
                                    <div>
                                        <NavLink className="navlinks" to="/service"><SearchIcon /></NavLink>
                                        <NavLink className="navlinks" to="/cart"><ShoppingCartIcon /> Cart</NavLink>
                                    </div>


                                </ul>
                            </nav>
                        </>)
                }

            </div>
        </>
    )
}


export default Navbar
